.App {
  text-align: center;
}

.TodosForm {
  display: flex;
  flex-direction: column;
  align-items: center;

  &__title {
    box-sizing: border-box;
    display: block;
    margin-bottom: 10px;
    padding: 0;
    width: 200px;
    height: 25px;
  }
}

.UsersList {
  margin-bottom: 10px;
  width: 200px;
  height: 25px;
}

.Submit {
  width: 100px;
  height: 30px;
}

.Warning {
  margin-bottom: 10px;
  color: red;
}

.hidden {
  display: none;
}
